<template>
    <div class="nut-luckdraw" ref="luckdraw" :style="{width:luckWidth, height:luckheight}">
        <div class="lucktable" :style="{transform: rotateAngle, transition: rotateTransition}">
            <canvas id="canvas" ref="canvas">
                æµè§å¨çæ¬è¿ä½
            </canvas>
            <div class="prize">
                <div v-for="(item, index) in prizeList" class="item" :style="getRotateAngle(index)" :key="index">
                <slot name="item" :item="item"></slot>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'nut-luckdraw',
    mounted() {
      this.init();
    },
    props: {
      luckWidth: {
        required: true
      },
      luckheight: {
        required: true
      },
      prizeList: {
        required: true
      },
      turnsNumber: {
        default: 5
      },
      styleOpt: {
        default: () => {
          return {
            // æ¯ä¸åæå½¢çèæ¯è²,é»è®¤å¼,å¯éè¿ç¶ç»ä»¶æ¥æ¹å
            prizeBgColors: ['rgb(255, 231, 149)','rgb(255, 247, 223)','rgb(255, 231, 149)','rgb(255, 247, 223)','rgb(255, 231, 149)','rgb(255, 247, 223)'],
            // æ¯ä¸åæå½¢çå¤è¾¹æ¡é¢è²,é»è®¤å¼,å¯éè¿ç¶ç»ä»¶æ¥æ¹å
            borderColor: '#ff9800',
          }
        }
      },
      turnsTime: {
        // è½¬å¨éè¦æç»­çæ¶é´(ç§)  
        default: 5
      }
    },
    data() {
      return {
        winningPrize: 0,
        // å¼å§è½¬å¨çè§åº¦
        startRotateDegree: 0,
        // è®¾ç½®æéé»è®¤æåçä½ç½®,ç°å¨æ¯é»è®¤æå2ä¸ªæå½¢ä¹é´çè¾¹çº¿ä¸
        rotateAngle: 0,//`rotate(30deg)`
        rotateTransition: '',
      }
    },
    methods: {
      // æ ¹æ®indexè®¡ç®æ¯ä¸æ ¼è¦æè½¬çè§åº¦çæ ·å¼
      getRotateAngle(index) {
        const angle = 360 / this.prizeList.length * index + (180 / this.prizeList.length);
        return {
          transform: `rotate(${angle}deg)`
        };
      },
      // åå§ååå½¢è½¬çcanvas
      init() {
        const data = this.styleOpt;
        const prizeNum = this.prizeList.length;
        const {prizeBgColors, borderColor} = data;
        // å¼å§ç»ç»
        const canvas = this.$refs.canvas;
        const ctx = canvas.getContext("2d");
        const canvasW = this.$refs.canvas.width = this.$refs.luckdraw.clientWidth; // ç»æ¿çé«åº¦
        const canvasH = this.$refs.canvas.height = this.$refs.luckdraw.clientHeight; // ç»æ¿çå®½åº¦
        // translateæ¹æ³éæ°æ å°ç»å¸ä¸ç (0,0) ä½ç½®
        ctx.translate(0, canvasH);
        // rotateæ¹æ³æè½¬å½åçç»å¾ï¼å ä¸ºæå­æ¯åå½åæå½¢ä¸­å¿çº¿åç´ç
        ctx.rotate(-90 * Math.PI / 180);
        // åç¯çå¤åçåå¾,å¯ç¨æ¥è°æ´åçå¤§å°æ¥éåºå¤é¨çå­çå¤§å°
        const outRadius = canvasW / 2 - 1;
        // åç¯çååçåå¾
        const innerRadius = 0;
        const baseAngle = Math.PI * 2 / prizeNum; // æ¯ä¸ªå¥é¡¹æå è§åº¦æ°
        ctx.clearRect(0, 0, canvasW, canvasH); //å»æèæ¯é»è®¤è²
        ctx.strokeStyle = borderColor; // è®¾ç½®ç»å¾çº¿çé¢è²
        for (let index = 0; index < prizeNum; index++) {
          const angle = index * baseAngle;
          ctx.fillStyle = prizeBgColors[index]; //è®¾ç½®æ¯ä¸ªæå½¢åºåçé¢è²
          ctx.beginPath(); //å¼å§ç»å¶
          // æ ååå¼§ï¼arc(x,y,radius,startAngle,endAngle,anticlockwise)
          ctx.arc(canvasW * 0.5, canvasH * 0.5, outRadius, angle, angle + baseAngle, false);
          ctx.arc(canvasW * 0.5, canvasH * 0.5, innerRadius, angle + baseAngle, angle, true);
          ctx.stroke();
          ctx.fill();
          ctx.save();
        }
      },
      // è½¬å¨èµ·æ¥
      rotate(index) {
        const turnsTime = this.turnsTime;
        const rotateAngle = this.startRotateDegree + this.turnsNumber * 360 + 360 - (180 / this.prizeList.length + 360 / this.prizeList.length * index) - this.startRotateDegree % 360;
        this.startRotateDegree = rotateAngle;
        this.rotateAngle = `rotate(${rotateAngle}deg)`;
        this.rotateTransition = `transform ${turnsTime}s cubic-bezier(0.250, 0.460, 0.455, 0.995)`;
        setTimeout(() => {
          this.$emit('endTurns');
        }, turnsTime * 1000 + 500)
      },
    },
}
</script>